.table {
  // Bottom align for column headings
  > thead > tr > th {
    vertical-align: bottom;
  }

  // Account for multiple tbody instances
  > tbody + tbody {
    border-top: 2px solid @table-border-color;
  }
}


.table-default {
  border: 1px solid @table-border-color;
  > thead,
  > tbody,
  > tfoot {
    > tr {
      > th,
      > td {
        border: 1px solid @table-border-color;
      }
    }
  }
}


.table-default {
    > thead {
      > tr {
        > th,
        > td {
          border-bottom-width: 1px;
        }
      }
    }
    
    > tbody > tr:nth-child(odd) {
      >  td,
      >  th{
        background-color: #f5f7fa;
      }
      
   }

   th:hover {
    border-right-width: 0;
    border-bottom-width: 0;
    border-left-width: 0;
    box-shadow: inset 0 -3px #5e99ff;
     >td {
        background-color: #cedffd !important;
     }
  }
}

.table-bordered > thead > tr {
  > th,
  > td {
      border-bottom-width: 1px;
  }
}

.table-hover {
  > tbody > tr:hover {
    border:0px;
    box-shadow: inset 0 -3px #5e99ff;
  }
}


.table-nav {
  position: relative;
  right: 12px;
  bottom: 0;
  float: right;
  &:before{
     display: inline-block;
    font-family: "syart-icons";
    font-style: normal;
    font-weight: normal;
    text-decoration: inherit;
    content: "\e089";
  }

}

.table-nav-figures {
  position: relative;
  bottom: 0;
  left: 4px;
  float: right;
}
.table-nav-figures:before {
  display: inline-block;
  font-family: "syart-icons";
  font-style: normal;
  font-weight: normal;
  text-decoration: inherit;
  content: "\e089";
}

.table {
  > thead,
  > tbody,
  > tfoot {
    > tr > td.success {
        padding: @table-cell-padding;
        line-height: @line-height-base;
        vertical-align: top;
        border-top: 1px solid @table-border-color;
        > td{
          background-color: #4ac764;
        }
    }

    > tr > td.info,
    > tr.info > td{
       background-color: #d9edf7;
    }

    > tr > td.warning,
    > tr.warning > td{
       background-color: #d9edf7;
    }

    > tr > td.danger,
    > tr.danger > td{
       background-color: #e73858;
    }

  }

}

.table-hover {
    > tbody {
         >tr > td.success:hover, 
         > tr > th.success:hover,
         > tr.success:hover > td,
         > tr.success:hover > th {
            background-color: #3abd56;
         }

         >tr > td.info:hover, 
         > tr > th.info:hover,
         > tr.info:hover > td,
         > tr.info:hover > th {
            background-color: #c4e3f3;
         }


         >tr > td.warning:hover, 
         > tr > th.warning:hover,
         > tr.warning:hover > td,
         > tr.warning:hover > th {
            background-color: #f79c27;
         }

         >tr > td.danger:hover, 
         > tr > th.danger:hover,
         > tr.danger:hover > td,
         > tr.danger:hover > th {
            background-color: #e73858;
         }
    } 
}