.syMenuSiderBar {
	background: @symenu-bg-color-deep;
}

.syMenuOptions {
  margin-top: 0px;
  margin-left: 0px;
  padding: 0px;
  background: @symenu-bg-color-deep;
	  > li {
	  	  text-align: center;
		  list-style-type: none;
		  height: 100px;
		  width: 100px; 
		  > a {
		  color: #3E474E;
		  text-decoration: none;
		  display:block;
		  font-size:11px;
		  padding-top: 10px;
		  height: 100%;
		  	> span {
			  font-size: 25px;
			  	> label {
				  display:block;
				  font-size: 12px;
				  font-weight: normal;
				  color: #fff;
				  cursor: pointer;
				}
			}
		}
	  }
	 > li.active {
		  background-color: white;
		  color:black;
	}
  	>  li:not(.active) > a > span {
	  color: white;
	}

	> li.active label, 
	> li.active > a > span {
		color: @symenu-bg-color-deep !important;
	}

	> li:hover:not(.active) {
		background-color: @symenu-bg-color-deep-hover;
		color: #ADADAD;
	}

	> li:not(.active) > a {
    	color: #fff;
	}
}